
import Vue from 'vue';
import { mapState, mapMutations } from 'vuex';
import Calendar from 'vux/src/components/calendar/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'day',
data() {
return {

}
},
computed: {
...mapState({
list: state => state.common.day.list,
vm: state => state.common.day.vm
})
},
components: {
Calendar,
XHeader
},
created() {
this.handleQuery();
},
methods: {
...mapMutations({
handleQuery: 'common/day/getList'
}),
handleGoTo(item) { //跳转
this.$store.commit('common/day/clear',item);
this.$router.push({
path: '/day/form',
query: {
id: item ? item.id : '',
}
});
},
handleDelete(taskId) {
const _this = this;
this.$vux.confirm.show({
title:"删除提示",
content:"确定要删除该条记录?",
onCancel () {
_this.$vux.toast.text('已取消删除', 'top');
},
onConfirm () {
_this.$store.commit({
type: 'common/day/delete',
id: taskId
})
}
})
}
}
}



